// lib/valorant.ts
import axios from 'axios';

const BASE_URL = 'https://valorant-api.com/v1';

// Typ f체r einen Agent (gek체rzt)
export interface Agent {
  uuid: string;
  displayName: string;
  description: string;
  displayIcon: string;
  role: {
    displayName: string;
  } | null;
}

export async function getAgents() {
  const response = await axios.get(`${BASE_URL}/agents?language=de-DE`);
  // WICHTIG: Filtern, da die API Duplikate und unspielbare Chars enth채lt
  const agents = response.data.data.filter((agent: any) => agent.isPlayableCharacter === true);
  
  return agents.map((agent: unknown) => ({
    uuid: agent.uuid,
    displayName: agent.displayName,
    description: agent.description,
    displayIcon: agent.displayIcon,
    role: agent.role ? agent.role.displayName : 'Unbekannt',
  }));
}

